import * as React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import { isNaN, PluginRoles } from '@ali/4ever-bamboo';
import { FontSizeSelect } from '@ali/we-toolbar';
import { NewToolbarMode as ToolbarMode } from '@ali/4ever-component';
import { setSz } from '../actions';
import { useDefaultSz } from '@ali/4ever-component';
import styled from 'styled-components';

const MemoFontSizeSelect = React.memo(FontSizeSelect);

const Wrapper = styled.div`
  .select_trigger_wrap .select_left_wrap {
    font-size: 1
  }
`;

const style: React.CSSProperties = {
  fontSize: 14.5,
  minWidth: 25,
};

export const szList = [9, 10, 11, 12, 14, 16, 18, 20, 22, 24, 26, 28, 36, 42, 48, 72];

export interface SzButtonProps {
  controller: Controller;
  locale: Record<string, string>;
  toolbarMode?: ToolbarMode;
}

export default function SzSimpleButton(props: SzButtonProps) {
  const { controller, locale, toolbarMode, ...rest } = props;
  const defaultSz = useDefaultSz();
  let sz: string | number | null = controller.query('getSize', defaultSz);
  if (isNaN(sz)) {
    sz = locale.buttonTitle;
  }
  const { isCollapsedAtEdge } = controller.value.selection;
  const isDisabled =
    isCollapsedAtEdge
    || controller.query('isSelectionInImage')
    || controller.query('isSelectionInHr')
    || controller.query('isSelectionInLinkCard');

  const handleChange = React.useCallback((size: number) => controller.run('onAction', setSz(size)), [controller]);
  return (
    <Wrapper>
      <MemoFontSizeSelect
        allowInput={false}
        {...rest}
        testid="bi-toolbar-sz"
        sizes={szList}
        disabled={isDisabled}
        // @ts-ignore 字号是 string 还是 number？
        value={`${sz}`}
        onChange={handleChange}
        bordered={false}
        shortcutText={locale.buttonTitle}
        style={style}
        role={PluginRoles.szSelect}
      />
    </Wrapper>
  );
}
